import React, { useState } from 'react';
import { useLocation, Link } from 'react-router-dom';

const ThankYouPage = () => {
    const location = useLocation();
    const orderId = location.state?.orderId || 'BRAK-ID';
    const [copyStatus, setCopyStatus] = useState(''); // 'copied', 'error', ''

    const handleCopyClick = async () => {
        try {
            await navigator.clipboard.writeText(orderId);
            setCopyStatus('copied');
            setTimeout(() => setCopyStatus(''), 2000); // Clear message after 2 seconds
        } catch (err) {
            console.error('Failed to copy: ', err);
            setCopyStatus('error');
            setTimeout(() => setCopyStatus(''), 2000); // Clear message after 2 seconds
        }
    };

    return (
        <div className="container mt-5 pt-5 text-center">
            <div className="py-5">
                <i className="bi bi-check-circle-fill text-success" style={{ fontSize: '4rem' }}></i>
                <h1 className="display-4 mt-3">Dziękujemy!</h1>
                <p className="lead">Twoje zapytanie o zamówienie niestandardowe zostało wysłane.</p>
                <hr className="my-4" />
                <p>Twój unikalny numer zamówienia to:</p>
                <div className="d-flex justify-content-center align-items-center mb-3">
                    <h3 className="bg-light p-3 rounded me-2"><strong>{orderId}</strong></h3>
                    <button className="btn btn-outline-secondary" onClick={handleCopyClick}>
                        <i className="bi bi-clipboard"></i> Kopiuj
                    </button>
                </div>
                {copyStatus === 'copied' && <p className="text-success">Skopiowano!</p>}
                {copyStatus === 'error' && <p className="text-danger">Błąd kopiowania.</p>}
                <p className="mt-3">Prosimy o zachowanie tego numeru. Będzie on potrzebny w dalszej komunikacji.</p>
                <Link to="/" className="btn btn-primary mt-4">Powrót na stronę główną</Link>
            </div>
        </div>
    );
};

export default ThankYouPage;